.WhatIDo {
    padding-top: 20vh;
    padding-bottom: 30vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    /* flex-direction: row; */
    /* align-items: center;  */
     /* justify-content: center; */
     /* justify-items: center; */
    /* scale: 1.2; */
    /* height: 50vh; */
    /* outline: 3px solid cyan; */
}

.first-div-main {
    /* padding-top: 10vh;
    padding-left: 4vh;
    padding-right: 4vh; */
    padding: 20px;
    /* outline: 3px solid cyan; */
    /* margin: 10px; */

}

.first-div {
    display: flex;
    justify-self: flex-end;
    border-radius: 10px;
    background-color: #151E2D;
    width: 35vw; 
    padding-bottom: 20px;   
    /* outline: 3px solid red; */

}

.title-first-div {
    font-size: 4rem;
    color: white;
    font-family: Raleway-ExtraBold;
}

.description-first-div {
    font-size: 1rem;
    color: white;
    padding-top: 3vh;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.learn-more {
    font-size: 0.8rem;
    padding-top: 3vh;
    color: white;
    font-family: Raleway-ExtraBold;
}

.bottom-border-learn {
    padding-top: 0.2vh;
    background-color: white;
    width: 5vw;
    height: 1px;
}

/* ------------ Second Div --------------- */

.second-div{
    height: inherit;
    display: flex;
    justify-self: flex-start;
}
.second-div-main{

    margin-left: 5vw;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    height: 100%;
    
}
.display-engine{
    margin-bottom: 1vh;
    background-color: #00000010;
    border-radius: 10px;
    width: 30vw;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.display-engine:hover{
    background-color: #151E2D;
    transition: 0.2s;
}
.engine-logo{
    margin-right: 1vw;
    border-radius: 50%;
    background-color: #151E2D;
    width: 50px;
    height: 50px;
    min-width: 50px;
    min-height: 50px;
    margin-left: 1vw;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.engine-logo img{
    object-fit: cover;
    width: 50%;
    height: 50%;
}

.display-engine:hover .engine-logo{
    background-color: #FFFFFF;
    transition: 0.2s;
}
.engine-title{
    transition: 0.2s;
    font-size: 1.5rem;
    color: #151E2D;
    font-family: Raleway-ExtraBold;
}
.display-engine:hover .engine-title{
    color: #FFFFFF;
    transition: 0.2s;
}
.engine-description{
    transition: 0.2s;
    font-size: .8rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #151E2D;
}
.display-engine:hover .engine-description{
    transition: 0.2s;
    color: #FFFFFF;
}
.engine-logo-png{
    /* width: 27px; */
    transition: 0.2s;
    /* height: 27px; */
}
.display-engine:hover .engine-logo-png{
    transition: 0.2s;
    filter: brightness(1) invert(1);
}
@media (max-width: 480px) {
    .WhatIDo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        scale: 1;
    }
    .first-div {
        width: 90vw;
        height: 50vh;
    }

    .bottom-border-learn {
        width: 18vw;
    }
    .title-first-div {
        font-size: 3.5rem;
    }

    .second-div-main{
        margin-left: 0vw;
        margin-top: 5vh;
    }
    .display-engine{
        width: 90vw;
        height: 12vh;
    }
}